<template>
	<div class="my-bank-card-set-name">
		<mt-field label="请输入别名" placeholder="支持汉字、数字和字母" v-model="DETAIL.ALIAS" :attr="{ maxlength: 10 }"></mt-field>
		<div class="button-bottom">
			<mt-button @click="submit" type="primary" size="large" :disabled="!DETAIL.ALIAS">确定</mt-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/my/my-bank-card';

export default {
	data() {
		return {
			DETAIL: {}
		};
	},
	methods: {
		submit() {
			if(this.DETAIL.ALIAS && !this.$validate.isAlias(this.DETAIL.ALIAS)){
				this.$alert('别名输入格式不正确，请输入数字、字母或汉字', {
                    btn: { text: '确定' }
                });
                return;
			}
			service.renameBankCard({
				ACCOUNT: this.DETAIL.ACCOUNT,
				ALIAS: this.DETAIL.ALIAS
			}).then(data => {
				this.$ls.setItem('bankCards', this.DETAIL);
				history.back();
			});
		}
	},
	created(){
		this.DETAIL = this.$ls.getItem('bankCards');
	}
};

</script>

<style lang="scss">
	
.my-bank-card-set-name{
	.mint-cell:after{
		background-color: transparent;
	}
}

</style>


