import ajax from '@/services/ajax';
import api from '@/services/api';

export default {
    // 获取已挂取的银行卡
    getBankCardList: data => {
        return ajax.postAfterLoginQueryList('990600', data, api.getBankCardList);
    },
    // 获取已挂取的银行卡详情
    getBankCardDetail: data => {
        return ajax.postAfterLoginQuery('990600', data, api.getBankCardDetail);
    },
    // 删除已挂取的银行卡
    deleteBankCard: data => {
        return ajax.postAfterLoginUpdate('990600', data, api.deleteBankCard);
    },
    // 设置已挂取的银行卡的别名
    renameBankCard: data => {
        return ajax.postAfterLoginUpdate('027001', data, api.renameBankCard);
    },
    // 设置已挂取的银行卡为主账号
    defaultBankCard: data => {
        return ajax.postAfterLoginUpdate('031002', data, api.defaultBankCard);
    },
    // 获取未挂取的账号
	getBankCardAccount: data => {
        return ajax.postAfterLoginQueryList('', data, api.getBankCardAccount);
    },
    // 添加未挂取的账号
    addBankCardAccount: data => {
        return ajax.post(api.addBankCardAccount, data);
    },
    // 添加银行卡
    addBankCard: data => {
        return ajax.post(api.addBankCard, data);
    },
    // 获取信用卡
    getBankCreditList: data => {
        return ajax.postAfterLoginQueryList('012022', data, api.getCreditList);
    },
    // 新增信用卡
    addCredit: data => {
        return ajax.postAfterLoginUpdate('980900', data, api.addCredit);
    },
    // 删除信用卡
    deleteCredit: data => {
        return ajax.postAfterLoginUpdate('980900', data, api.addCredit);
    }
};



