<template>
	<div class="my-bank-card-account">
		<div class="account-msg cell-part">
			<div class="name">{{account.name}}</div>
			<div class="bank">
				{{account.bankName}}
				<span class="bank-type">{{account.type}}</span>
			</div>
			<div class="number">{{account.account | formatAccount}}</div>
			<ul class="type">
				<li>人民币卡</li>
				<li>账户类型：III类</li>
				<li>账户状态：正常正</li>
			</ul>
		</div>
		<div class="tip-hint">
			该账户状态异常，无法添加到手机银行，请到我行网点查询，或致电96669。
		</div>
		<div class="cell-part">
			<mt-field class="account-rename" label="设置别名" placeholder="（选填）请输入别名，例如：（理财卡）" v-model="rename"></mt-field>
		</div>
		<div class="cell-part">
			<cell-password :options="cellPassword" ></cell-password>
		</div>
		<div class="cell-warn">
			交易密码错误，请重新输入！
		</div>
		<div class="button-bottom">
			<mt-button @click="submit" size="large" type="primary">添加到手机银行</mt-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/my/my-bank-card';

export default {
	data() {
		return {
			account: {},
			rename: '',
			password: '1111',
			cellPassword: {
                title: '交易密码',
                rule: 'number'
            }
		};
	},
	methods: {
		submit() {
			var _error = [];
			var self = this;
			if(this.rename && !this.$validate.isAlias(this.rename)){
				_error.push('别名输入格式不正确，请输入数字、字母或汉字');
			}
			if( this.$validate.isEmpty( this.password ) ) {
                _error.push( '交易密码不能为空。' );
            }
            if( _error.length > 0 ) {
            	this.$alert(_error[0], {
                    btn: { text: '确定' }
                });
                return;
            }
            service.addBankCardAccount({
            	id: self.account.id,
            	alias: self.account.alias,
            	password: self.account.password
            }).then(data => {
            	this.$router.push({name: 'MyBankCardSuccess', params: { type: 'add' }});
            });
		},
		getBankCardDetail(id) {
			service.getBankCardDetail({
				id: id
			}).then(data => {
				this.account = data.data;
			});
		}
	},
	created(){
		this.getBankCardDetail(this.$route.params.id);
	}
};

</script>

<style lang="scss">

.my-bank-card-account{
	.account-msg{
		position: relative;
		background-color: #fff;
		padding: 30px 50px 0;
		height: 284px;
		@include image-set('../../../../assets/img/bank/card/bg');
		.name{
			font-size: 15px;
			color: #666666;
			margin-bottom: 20px;
		}
		.bank{
			font-size: 15px;
			color: #666666;
			margin-bottom: 10px;
			.bank-type{
				display: inline-block;
                margin-left: 20px;
                width: 110px;
                height: 36px;
                font-size: 12px;
                color: #B5B6BE;
                text-align: center;
                line-height: 36px;
                @include image-set('../../../../assets/img/bank/card/label');
			}
		}
		.number{
			font-size: 15px;
			color: #272727;
			font-weight: normal;
            letter-spacing: 2.3px;
            font-family: Farrington7B_Qiqi, "Helvetica Neue", Helvetica, STHeitiTC, sans-serif;
		}
		.type{
			position: absolute;
			display: table;
			width: 100%;
			left: 0;
			bottom: 20px;
			padding: 0 50px;
			font-size: 13px;
			color: #fff;
			li{
				display: table-cell;
			}
			li:nth-child(2){
				text-align: center;
			}
			li:nth-child(3){
				text-align: right;
			}
		}
	}
}

</style>


